<template>
  <div class="item__content">
    <h3>Доставка:</h3>

    <ol>
      <li v-for="(delivery, index) in deliveries" :key="delivery.id">
        {{ delivery.title }} –
        {{
          deliveryPretty[index] === '0'
            ? 'бесплатно. Список и адреса магазинов Вы можете посмотреть здесь'
            : `${deliveryPretty[index]}  ₽`
        }}
      </li>
    </ol>

    <h3>Возврат:</h3>

    <p>
      Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br />
      Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране, в которой
      Вы осуществили покупку. БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br />
      Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов, позвоните по телефону 8
      800 600 90 09<br />
    </p>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
  computed: {
    deliveries() {
      return this.$store.state.deliveries;
    },
    deliveryPretty() {
      return this.deliveries.map((item) => numberFormat(item.price));
    },
  },
  created() {
    this.$store.dispatch('loadDeliveries');
  },
};
</script>
<style>
.item__content ol {
  margin: 0;
  padding-left: 20px;
}

.item__content li {
  line-height: 28px;
  font-weight: 300;
}
</style>
